import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import { Card, Row, Col, message } from 'antd';
import Page from 'components/LayoutComponents/Page';
import { BtnAddNew, Btn } from 'components/common/button';
import { TableData, ColEditDelete } from 'components/common';
import { getQueryParams } from 'utils/urlUtil';
import { getRankConfigs, deleteRankConfig, downloadFile } from 'services/rankConfig';
import FormSearchRankConfig from './FormSearchRankConfig';

class ListRankConfigPage extends Component {
  state = {
    reload: 0,
    paramSearch: {},
  };

  componentDidMount = () => {
    this.handleChangeQueryParams();
  };

  componentWillReceiveProps = () => {
    this.handleChangeQueryParams();
  };

  urlFormNew = '#/rank-config/new';
  urlFormEdit = '#/rank-config/edit';

  handleChangeQueryParams = () => {
    this.setState({ paramSearch: getQueryParams() });
  };

  handleDelete = async id => {
    await deleteRankConfig(id);
    message.success('Xóa thành công');
    this.setState({ reload: this.state.reload + 1 });
  };

  exportRankConfig = () => {
    const { paramSearch } = this.state;
    downloadFile(paramSearch);
  };

  render() {
    const columns = [
      {
        title: 'Mã ngạch',
        dataIndex: 'id',
        key: 'id',
      },
      {
        title: 'Chức danh',
        dataIndex: 'label',
        key: 'label',
      },

      {
        title: 'Thao tác',
        dataIndex: 'operation',
        render: (text, record) => (
          <ColEditDelete
            hrefEdit={`${this.urlFormEdit}/${record.id}`}
            onConfirmDelete={() => this.handleDelete(record.id)}
          />
        ),
      },
    ];
    return (
      <Card title="">
        <Row>
          <Col span={24}>
            <FormSearchRankConfig />
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <div className="btn-group" style={{justifyContent: 'flex-start'}}>
              <BtnAddNew href={this.urlFormNew} />
              <Btn onClick={this.exportRankConfig} icon="download">
                Download
              </Btn>
            </div>
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <TableData
              reload={this.state.reload}
              paramSearch={this.state.paramSearch}
              service={getRankConfigs}
              columns={columns}
              rowKey="id"
            />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default withRouter(props => (
  <Page {...props}>
    <ListRankConfigPage {...props} />
  </Page>
));



